/*
This is the styling page for the landing section of the portfolio.
On the left of the page is a welcome message styled with highlighted words
The self-portrait is positioned in the second half of the page
*/

/* Height is equal to 100% view height - the height of the navbar (5rem) */

#intro {
  height: calc(100vh - 5rem);
  width: 80vw;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}

#intro-content {
  height: calc(100vh - 5rem);
  width: 100vw;
  padding: 2rem;
  font-size: 3.4rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#hello {
  font-size: 1.75rem;
  font-weight: 100;
  margin-bottom: 15px;
}

#introName {
  color: rgb(240, 156, 1);
}

#web-dev {
  margin-bottom: 20px;
}

.intro-paragraph {
  font-size: 1.2rem;
  font-weight: 100;
  line-height: 1.25;
  color: rgb(240,240,240);
  max-width: 60vw;
}

.intro-text {
  margin-bottom: 15px;
}

#intro-connect {
  font-size: 1.38rem;
  transform: translateY(20px);
}

/* Self-Portrait */

#portrait {
  object-fit: contain;
  height: 150vh;
  width: 400px;
  position: absolute;
  top: 4rem;
  right: 10%;
  z-index: -1;
}

/* Let's Connect */

#right-arrow:hover {
  transform: translateX(14px);
}

#intro-connect:hover {
  cursor: pointer;
}

#right-arrow {
  transition: 0.25s ease-in-out;
  margin-left: 12px;
  color: rgb(240, 156, 1);
}